/* -------------------
------ Grid ----------
------------------- */

.container {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-flow: row wrap;
        -ms-flex-flow: row wrap;
            flex-flow: row wrap;
    max-width: 100%;
    margin-left: auto;
    margin-right: auto;
}

@media (min-width: $(tablet)) {

    .container {
        display: -webkit-box;
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex;
        -webkit-flex-flow: row wrap;
            -ms-flex-flow: row wrap;
                flex-flow: row wrap;
        max-width: 740px;
        margin-left: auto;
        margin-right: auto;
    }
}

@media (min-width: $(desktop)) {

    .container {
        display: -webkit-box;
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex;
        -webkit-flex-flow: row wrap;
            -ms-flex-flow: row wrap;
                flex-flow: row wrap;
        max-width: 960px;
        margin-left: auto;
        margin-right: auto;
    }
}

@media (min-width: $desktop-large) {

    .container {
        display: -webkit-box;
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex;
        -webkit-flex-flow: row wrap;
            -ms-flex-flow: row wrap;
                flex-flow: row wrap;
        max-width: 1160px;
        margin-left: auto;
        margin-right: auto;
    }
}
